@charset "UTF-8";
@import "variables";
@import "icons";
@import "../../../sass/reset";
@import "../../../sass/func";
html,
body {
  position: relative;

  margin: 0;
  padding: 0; // height: 100%;
  // width: 100%;
  // overflow: hidden;

  font-family: "Helvetica Neue", Helvetica, STHeiTi, sans-serif;

  color: #333;

  -webkit-overflow-scrolling: touch !important;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  text-decoration: none;
}

button {
  border: 0;
  padding: 0;

  outline: none;
}

input {
  margin: 0;
  border: 0;
  padding: 0;
  height: 100%; // font-size: pxToRem(32px);

  outline: none; // &::-webkit-input-placeholder {
  //   color: #ccc;
  // }
}

.jms-header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;

  height: pxToRem(88px);

  line-height: pxToRem(88px);

  background-color: #fff;

  box-shadow: 0 0 pxToRem(20px) 0 rgba(0, 0, 0, .15);
  .btn-back {
    position: absolute;

    width: pxToRem(80px);
    height: 100%;

    background: url("data:img/jpg;base64,iVBORw0KGgoAAAANSUhEUgAAABEAAAAeBAMAAAA4FegcAAAAFVBMVEUAAAAzMzMzMzMzMzMzMzMzMzMzMzO27Uu7AAAABnRSTlMAJ+a7CiGR3gNEAAAAJUlEQVQY02OAAkEYg1EpAMoSSnaACqmZ0E8IwULIDqCgUgAihADidA8zGXe3pgAAAABJRU5ErkJggg==") no-repeat;
    background-position: pxToRem(20px) 50%;
    background-size: pxToRem(18px) pxToRem(30px);
  }
  h1 {
    font-size: pxToRem(30px);
    text-align: center;

    color: #333;
  }
  .right {
    position: absolute;
    top: 0;
    right: 0;

    width: pxToRem(80px);
    height: 100%;
    i {
      position: absolute;
      top: 50%;
      left: 50%;

      transform: translate(-50%, -50%);
    }
  }
  .icon-home {
    @include background("data:img/jpg;base64,",
    36px,
    30px);
  }
}

.bg-grey {
  background-color: #f6f6f6;
}

.bg-white {
  background-color: #fff;
}

.arrow {
  position: absolute;
  top: 50%;
  right: pxToRem(28px);

  width: pxToRem(14px);
  height: pxToRem(24px);

  background: url("data:img/jpg;base64,") no-repeat center;
  background-size: contain;

  transform: translate(0, -50%);
}

// list 加载loader
.list-loader {
  position: relative; // margin-right: pxToRem(30px);

  height: pxToRem(100px);

  font-size: pxToRem(24px);
  line-height: pxToRem(100px);
  text-align: center;

  color: #999;
  .spinner {
    display: inline-block;

    position: relative;
    top: auto;
    left: auto;

    margin: 0;
    padding: 0;

    vertical-align: middle;
  }
}

.input-wrapper {
  position: relative;

  margin-bottom: pxToRem(30px);
  height: pxToRem(90px);

  line-height: pxToRem(90px);
  &:after {
    @include border(bottom,
    #eeeff1);
  }
  i {
    margin-right: pxToRem(10px);

    vertical-align: middle;
  }
  label {
    padding-right: pxToRem(24px);
    width: pxToRem(184px);

    text-align: left;
  }
  input {
    width: pxToRem(540px);

    color: #333;
  }
}

.input-wrapper-code {
  @extend .input-wrapper;
  input {
    width: pxToRem(540px - 170px);
  }
  & > div {
    display: inline-block;

    width: pxToRem(160px);
  }
  .code {
    font-size: pxToRem(24px);

    color: #ffbf00;
    &.unabled {
      color: #999;
    }
  }
}

.item-wrapper {
  position: relative;

  padding-left: pxToRem(30px);
  height: pxToRem(100px);

  font-size: pxToRem(32px);
  line-height: pxToRem(100px);

  color: #333;
  background-color: #fff;
  input,
  p {
    display: inline-block;
    float: right;

    box-sizing: border-box;
    padding-right: pxToRem(58px);
    width: pxToRem(538px);

    font-size: pxToRem(32px); // text-align: right;
  }
  input {
    &::-webkit-input-placeholder {
      color: #ccc;
    }
    &[readonly] {
      color: #ccc;
    }
  }
  &:after {
    @include border(bottom,
    #e5e5e5);

    left: pxToRem(30px);
  }

  label {
    padding-right: pxToRem(24px);
    width: pxToRem(184px);

    text-align: left;
  }
}

.btn {
  display: block;

  margin: 0 auto;
  border-radius: pxToRem(4px);
  width: pxToRem(668px);
  height: pxToRem(88px);

  font-size: pxToRem(32px);
  line-height: pxToRem(88px);
  text-align: center;

  color: #fff;
  background: linear-gradient(to right, #f28d00, #fa0);

  box-shadow: 0 pxToRem(6px) pxToRem(20px) 0 rgba(250, 167, 0, .3);
  &.unabled {
    color: #999;
    background: #e5e5e5;

    box-shadow: 0 pxToRem(6px) pxToRem(20px) 0 rgba(0, 0, 0, .15);
  }
}

.btn-common {
  @extend .btn;

  width: pxToRem(690px);

  background: #ffbf00;

  box-shadow: none;
}
.btn-gray {
    @extend .btn;
    width: pxToRem(690px);
    background: #E5E5E5;
    color: #999;
    box-shadow: none;
}
.btn-radius {
  @extend .btn;

  border-radius: pxToRem(96px);
  width: 100%;
  height: pxToRem(96px);

  line-height: normal;

  color: #3b392b;
  background: linear-gradient(-104deg, #ffae00, #ffd400);

  box-shadow: 0 pxToRem(6px) pxToRem(20px) 0 #ffe88c;
}
